"use client";

import { useEffect } from "react";
import { useRouter } from "next/navigation";
import { EmailAuthProvider } from "firebase/auth";
import { auth } from "@/lib/firebase";

// We import firebaseui only on the client; this file is a client component.
import * as firebaseui from "firebaseui";

let firebaseUiInstance: firebaseui.auth.AuthUI | null = null;

export function FirebaseEmailAuth() {
  const router = useRouter();

  useEffect(() => {
    if (!firebaseUiInstance) {
      firebaseUiInstance =
        firebaseui.auth.AuthUI.getInstance() ?? new firebaseui.auth.AuthUI(auth);
    }

    const uiConfig: firebaseui.auth.Config = {
      signInOptions: [EmailAuthProvider.PROVIDER_ID],
      signInSuccessUrl: "/dashboard",
      callbacks: {
        signInSuccessWithAuthResult: () => {
          // Let Next handle navigation; prevent full page reload.
          router.replace("/dashboard");
          return false;
        },
      },
    };

    firebaseUiInstance.start("#firebaseui-auth-container", uiConfig);

    return () => {
      firebaseUiInstance?.reset();
    };
  }, [router]);

  return <div id="firebaseui-auth-container" />;
}

